      "title": "Erik Torres _SJS/TEN Liver Transplant  ",
      "description": "  Erik at 13 yrs. the oldest of 3 children was diagnosed with Steven Johnsons Syndrome/TEN in February 8 2013 (Originally misdiagnosed on 2/5/13) He had a severe allergic reaction to Azithromycin or Ibuprofen (there is no test to administer to verify which medication it was). He was hospitalized for 1 month - in which he spent 3 weeks in the ICU. Erik was Treated as a burn victim and had several eye procedures done while in the ICU. He lost 70% of his skin. He has currently been diagnosed with the vanishing bile duct syndrome. This is a very rare condition... none of the doctors we have seen have seen this in any patient before. So he will be placed on the transplant list in hopes of a new liver. Aside from his liver he developed scars in his eye lids. Which will require more surgeries.  Knowing that there is NO chance of a full recovery our hopes are that we can help him with any current and future medical needs.  So please if you are able to donate anything - It would mean so much to him/us. If you cant we understand. However We would ask that you please say a prayer for him and share this page...   THANK YOU so much for taking the time to read this. Thank you for any donations and specially for ALL YOUR PRAYERS!! GOD BLESS YOU ALL!!   *********   Erik a los 13 años. El mayor de 3 hijos se le diagnosticó síndrome de Steven Johnson/TEN en Febrero 8 2013 (originalmente diagnosticado en 2/5/ 13) había una reacción alérgica severa a la azitromicina o ibuprofeno (no existe una prueba para verificar cual fue).  El fue hospitalizado por un período de 1 mes - en el que durante 3 semanas fueron en la UCI. Erik fue tratado como una víctima de quemadura y tuvo varios procedimientos en los ojos durante este tiempo. Perdió 70% de su piel. Actualmente ha sido diagnosticado con síndrome de las vías biliares.  Esta es una afección muy rara... ninguno de los médicos que hemos consultado han visto esto. Por lo tanto estará en la lista de trasplante con la esperanza de un nuevo hígado. Aparte de su hígado Erik desarrolló unas cicatrices en sus párpados. Lo que requerirá más cirugías. Sabiendo que no hay posibilidad de una recuperación completa. Nuestra esperanza es de que lo podamos ayudar con sus actuales y futuras necesidades médicas.  Por lo tanto por favor si usted es capaz de donar hoy significaría mucho para él. Si no lo comprendemos. Sin embargo le pedimos que por favor diga una oración por él y comparta esta página...  MUCHAS GRACIAS por tomarse el tiempo de leer esto. Gracias por todas las donaciones y especialmente para TODAS SUS ORACIONES!  ",
