      "title": "Please Help Rosie  ",
      "description": " WARNING GRAPHIC IMAGES   Rosie a beautiful loyal Poodle mix was hit by a car and was dragged 5-7 feet- her family witnessed everything that was happening to Rosie. She suffered serious injuries such as having her right arm ripped open to the bone from underarm to wrist severe contusions to her neck and back; the impact caused Rosies ear to be torn off which exposed the cartilage. Her family immediately picked her up and sought help they took her to a place where they thought would help her; they took her to the Downey Shelter believing that the shelter would help keep Rosie alive. That was the furthest from reality. Luckily for the family Lisa Corona saw the family and asked if Rosie was sick once Lisa saw Rosie she knew that Rosie needed help-the shelter was not going to help Rosie but kill her once they convinced the family to surrender her.   Rosie is now at Dr. Werbers office receiving the care she needs to recover from her injuries she had surgery on Wednesday to re-attach her ear to her skull   she is having surgery again today to do a debridement on her right leg to completely clean the bone area   scrape off the necrotic skin   tissue from her ligaments. Rosie is going to need long term bandage changes while her right arm is allowed to grow new skin (think of very bad burns on a human)   they will continue the cleaning process to scrape the dead tissue every few days for a few months until she is completely healed.  Please donate any amount you can funds will be directly given to Dr. Werber for saving Rosies life.  Thank you for your generosity and love for Rosie and her family.     ",
