      "title": "Reggies War On Lymphoma ",
      "description": " On January 15 2014 Reggie was diagnosed with Stage 4 Large B-Cell Non-Hodgkins Lymphoma.   Reggie prefers to call it The Phoma....   He has started his first of 6-8 rounds of aggressive chemotherapy and unfortunately the bills are starting to pile up.  We were not prepared and are still waiting for his LTD to be processed.  In the meantime we are expected to pay not only the household bills but are hit with having to pay his insurance and medical expenses in order to continue treatment.  We have reduced the bills including giving up my car (loan and insurance costs) but the medical costs are coming in.  The goal above covers his out of pocket expenses so far insurance costs and what we anticipate still to come from surgeon and hospital.  We know everyone has their own problems but if you can help with a few dollars we would greatly appreciate anything to help this battle and allow Reggie to focus solely on his recovery and not the stress of finances.  Reggie is a proud man and hates to ask for anything from anyone so I took it upon myself to create this page and ask for help.  We appreciate all the amazing support from everyone keep those prayers coming. ",
