      "title": "In Loving Memory of Daniel",
      "description": "Finding the words to describe Daniel seems almost impossible. He was such a genuine loving and intelligent soul whose heart was made of gold. No amount of money in the world could heal the stinging pain of losing a person so precious.  However to help the Anders deal with the loss of their son brother grandson cousin and uncle we have opened this donation page. The money raised will be used to help defer the funeral costs and a portion will be passed along to Daniels dearest passion: Hylton Choir. We made the goal amount $7300 because he was born on July 3 1997 and we couldnt put a dollar amount on this devastating time. We thank you all so much for your priceless love support and prayers.",
