      "days_active": "1973",
      "days_created": "1973",
      "title": "Corona Family Support Fund",
      "description": "Hello Everyone   As some of you may already know My brother Marco was in a very serious Motorcycle accident Sunday October 12th 2014. If you didnt know. Im sure you can understand the horror we felt when we were notified. We did not have time to think. Just react. The shock is still sinking in.   Marco suffered multiple fractured vertebrae 7 broken ribs and a collapsed lung. The doctors had to perform a craniotomy to alleviate the hemorrhaging in his brain from the impact. His lungs and liver shifted in the crash and suffered internal bleeding from these injuries. He was in an induced coma for 5 days.    First and foremost my apologies if I did not personally inform you and you heard it through the grapevine. If you know my brother you know he is very private. And when it comes to family matters so am I. I had to ask Sonia his beautiful wife if I could open this account. They dont want to ask for money. But I have been here with them from the beginning and I can already see the struggles ahead. Everyone wants to know how they can help and Prayers are the most important. But they have a family and he is still in very critical condition. He has a long road ahead of him for recovery. The trips across the bridge and parking fees add up quickly.  And these will be necessary as outpatient rehab is in store. He wont be able to work for a while and Sonia as well. And you know as well as I do that the bills dont stop when life might feel like it has come to a screeching halt.    I humbly ask if you truly want to help please donate what you can. I created this account for anyone who wants to help but doesnt know how. It is always awkward to ask for things. I am not shy in asking for what I know will benefit my big brother and his beautiful family. My niece and nephew are their world. Im sure many of you know this. My brother is a hard workingman he works his fingers to the bone to provide for his family as Im sure you all do for your family. And right now this will not be possible until he fully recovers.   I thank you all for your time and your thoughts and prayers. I hope you know how much this will help and anything is appreciated.    Always with Love   Adrie on behalf of Marco Sonia Javier and Alexsandra Corona",
