      "description": "  Queridos amigos y familia de la industria como algunos de ustedes saben hace unos meses atras comence esta recaudación de fondos para ayudar a Octavio Hernández Díaz a conseguir los fondos para el tratamiento de su dama y compañera Rosalba Velasco. Octavio murio tratando de salvar a su dama. Me duele el corazon solo de pensar que estuvo solo en su partida. En nuestras últimas conversaciones hace una semana me decía lo mucho que la extrañaba y necesitaba a su lado. Les pido a ustedes ahora más que nunca su apoyo. No dejemos sola a Rosalba. Démosle a Octavio la tranquilidad de que sus amigos estamos aqui. Por favor de aportar algo y compartir este post. Se que son momentos difíciles para todos pero lo poco o mucho que puedan dar sumará si todos nos unimos. De esta forma Rosalba podrá solventar los gastos que esta situación le causarán y recibir el tratamiento necesario para combatir la terrible enfermedad que sufre. Aqui el link de gofundme  http://www.gofundme.com/ayudemosOctavio   Por favor hagan los depósitos aquí con sus tarjetas de crédito o débito no importa si son Mexicanas o de USA u otro país.  Octavio no quería usar una cuenta de México ya que le estaban quitando impuestos . Gracias por su apoyo. Lo que sea que puedan era de mucha ayuda. $50 $100 pesos o dolares! todo unido se suma. Y favor de postear este link para que más personas se enteren. Gracias!  Este es el texto que Octavio nos dejo buscando ayuda de todos nosotros antes de su partida.  Queridos amigos - por Octavio Hernandez   En las buenas y en las malas se le mide el pulso a la ciudad y a los amigos. En las buenas se se celebran los logros y se comparten como con El Arca de Neón (radio) Duelo deRebeldes (concurso) La Frontera Aquí (festival) el TijuaNeo (impreso y digital) etc. Con los que apoyamos con pasión y profesionalismo la cultura la música y el arte tanto local como nacional y californiana. En las malas se busca el apoyo y la solidaridad de los amigos y aliados en la eterna batalla de la vida. Hago pública la noticia de que en diciembre de 2013 le comenzó una extraña enfermedad a mi dama pareja socia y compañera de la vida Rosalba Velasco. Con dolores en las piernas y pérdida del equilibriomes tras mes fue empeorando hasta terminar lamentablemente en silla de ruedas. Es un trastorno neuorológico motriz que si tiene cura pero en Tijuana tras consultar a muchos médicos de varias especialidades no tuvimos suerte gastamos un dineral y nadie la pudo curar. Hace algunos meses la enfermedad nos superó y se fue con su familia a La Paz para consultar un especialista que le recomendaron y está en espera de que consigamos los recursos para poder llevarla con médicos alternativos en el DF y Monterreyciudades que tienen clínicas en las que puede recuperarse siguiendo el consejo de un médico amigo. Por tal razón iniciamos hoy una campaña para obtener apoyos en base a donaciones de la cantidad que les sea posible por este medio de GO FUND ME. E igual realizaremos algunos eventos a beneficio en Tijuana a los cuales les invitaremos en fecha próxima para que nos acompañen. Agradecemos a los amigos músicos que nos apoyarán e igual estamos abiertos a recibir propuestas de otros artistas actores bailarines o artistas plásticos etc que quieran apoyarnos en este difícil momento. Hemos sido fuertes ante este enorme reto porque sabemos que saldremos adelante con la ayuda de Dios los santos y los ángeles y de los que nos quieren y estiman y aprecian todo lo que hemos hecho por esta hermosa ciudad a la que amamos y por el tránsito de la cultura de Los Angeles a la frontera. Ahora nuestra prioridad es que mi ángel en desgracia se cure y vuelva a sonreírcomo antes.     Mil gracias a todos.  OCTAVIO HERNANDEZ DIAZ   1959 - 2015   D.E.P (R.I.P) ",
