      "title": "Kids Corner by Luv Pacs ",
      "description": "                         LUV PACS is a non-profit that helps foster children and other children in need in our community.   Children being placed in foster care receive a back pack filled with personal hygiene items and comfort items a homemade flannel quilt and a flannel laundry bag to match the quilt.   They also receive whatever basic item they they may require (ex. any type of clothing beds mattresses bedding etc) and these items are also given to Chilren in Child Protective Services.     These children also receive  whenever they are in need of them winter wear socks underwear whatever other clothing they are in need of and new supplies of personal hygiene items).    If only you could enjoy the looks on their faces when they receive these item they light up and cant beleive that someone would be so kind to do this for them.    Each dollar you donate to Luv Pacs will aid us in our vision to seek to enhance the lives of children everywhere.  Help us ensure that no child will be forgotten.    Luv Pacs is a 501c3 organization.  Tax receipts for your donation may be mailed upon request.                         ",
