      "title": "DMFC?2016",
      "description": "As early as I can remember music has been a major part of my life. In my earliest school days I remember always being interested in any instrument and the beautiful sounds they would make. From hand bells to drums and everything in between I knew this love was eternal. My mama always nurtured my desire by keeping instruments and music readily available for me and the guitar was the instrument that let me truly express myself.  She would always tell me a carpenter has to have good tools so does a musician. I picked up so many influences along the way from family and friends and anyone that would take the time to show me anything musically or on the guitar. Shoot....even if they didnt take the time I was always watching and learning. Fast forward 30  + years and the fire is still burning..the love is still there. So much that I want to share it with the world in the form of a record. With your help this can be possible. I know there are a lot of good ways to spend your hard earned money and I hope you will consider donating to my campaign were gonna have a lot of fun along the way!!",
