      "title": "Maria Bahena Corona",
      "description": "Maria Bahena was born February 2 1997. She was diagnosed with cancer at the tender age of 3 with treatments she was able to beat it.  At the age of 18 the cancer came back and shes been fighting for 41/2 months but the doctors say theres nothing else that they could do :(. But its all in gods hands. Please help the family Bahena in this difficult time.  Keep her and her family in your prayers. Thank you  we do appreciate it  Maria Bahena nació el día 2 de Febrero a la pequeña edad de 3 años le diagnosticaron con Cancer Con tratamientos luego vencerlo. Pero a la edad de 18 años le regreso tiene 4 meses y medio en hospital pelando con esta terrible enfermada los doctores ya no pueden hacer nada todo esta en la manos de dios por favor ayuden a la familia Bahena Corona en estos momentos tan difíciles se le agradece mucho. Muchas gracias   May 17 2015 Maria Bahena Corona Lost Her Battle With Cancer. Now She An Angel Resting With God And Feeling No Pain. Rest In Peace Maria Bahena Corona  El Dia 17 De Mayo Del 2015 Matia Bahena Corona Perdió Su Batalla Con El Cancer El Esta Con Dios Decanzando Y Sin Dolor. Descanse En Paz Maria Bahena Corona  ",
