      "days_active": "1770",
      "days_created": "1770",
      "title": "Matts Road To ATHENS",
      "description": "   Friends and Family  The sport of wrestling has given me so much over the past 35 years.  Now in the twighlight of my career I have a once in a lifetime opportunity and I need your help.  In September 2014 I set out on a journey to prove something to myself and teach my children a valuable lesson in goal setting hard work and committment. Most importanly I wanted to teach them how to handle winning and losing with humilty and grace. With my 9 year old son as my training partner/coach I lost over 50lbs and laced up my wrestling shoes to compete after a 15 year layoff; placing 3rd at the 2015 US Open Veterans Freestyle National Championships.  I thought the journey would end there and the mission would be complete; however I am now qualified to compete in the  World Championships  on October 13-18th in  Athens Greece . To have a chance to compete in the birth place of the Olympics on the same grounds as the original gladiators is every wrestlers dream. With your help I can not only accomplish those goals I orginally set out to achieve but also put an exclamation point on my career.  Thank you  Matt  I promise Ill train hard and try my best!",
