      "title": "Andrew The lil Bull Corona",
      "description": "My name is Andrew Corona and Im an ten year old amateur boxer. Ive been learning the sport of boxing since I was six years old I started competing at the age of eight. Im very dedicated to boxing and my academics. I work hard to be an honor student and a National Champion. I hope to inspire kids all around the world to work hard and do what they love as I have been inspired to do.   As I continue on this journey I hope to achieve many great accomplishments and continue to  be a great role and true inspiration to all those who contiue to follow me and stand by my side on this journey.    My next stop is Junior Olympic Nationals in Charleston West Virgina. I would love and appreciate any support I receive as I head out on this journey.     I truly appreciate my following and all the love and support from you all! I will make you all proud.   -The Lil Bull      ",
