      "title": "Jenny Kullers Car-B-Que fund. ",
      "description": " Dear friends in the vintage community our sweet sweet friend Jenny Kuller of   Redhead Sadie Vintage suffered a devastating loss on June 8th. While traveling home from The Vintage Marketplace in the Oaks event in Fallbrook CA last weekend Jenny had car trouble broke down a couple of times and eventually her van caught fire before they made it home. Much of her inventory was lost in the fire.    We are setting up this gofundme account to help Jenny replace items that were destroyed help with her repair bills and give her hope - the hope that comes from knowing that you have friends when you need them most.    Please help us put that beautiful smile back on Jennys face. Any amount that you can spare is so very much appreciated. We love our vintage community (vendors designers customers event organizers alike) and know that you will do what you can to support our Queen of Vintage Clothing and Accessories. ",
