      "title": "Help Mike Corona get to Worlds ",
      "description": "  Dear Friends and Family.   For those of you who do not know I have been training in the art of Muay Thai for about 4 years now. It has really become an eye opening experience which has allowed me to meet many loving and caring people of the Muay Thai community. I currently fight and train out of Sitan Gym in Queens New York under one of the best trainers in the world Aziz Nabih.   How I got started : About four years ago I was on my way to Sign up to Planet Fitness. After having gained the Freshman 15 in college I received a call from Local Union Number 3. I decided to get myself in order to keep up with the construction lifestyle and chaos. On my way to sign up I bumped into my cousin Joel Estevez who I cannot thank enough for introducing me to the sport who encouraged me to go to the gym with him. And it is honestly one of the best decisions I have ever made.   Purpose for Gofundme:  I competed In an amateur Muay Thai tournament this past March and have now qualified to represent Team USA in the Worlds Muay Thai tournament. The tournament will take place in Spain this November. Since I am still an amateur fighter I must come up with the funds for the trip myself.   How can you help:  I am asking for a small donation from people in order to help pay for my trip to Spain to compete in this tournament. There is no minimum and any dollar donated is greatly appreciated and a huge help. I promise that I will do my best and make everyone proud.   Thank you.",
