      "days_created": "1713",
      "title": "Corum Family Love Fund",
      "description": "Keith Corum his wife Kim and their son Caleb live in South Hill Virginia and are involved members in their community.  Keith is a local radio celebrity and has worked tirelessly to promote South Hill and the surrounding areas as a morning radio personality for the local radio station Rewind 101.9.  He is also a former News Editor for the local newspaper the South Hill Enterprise.  Keith has spent his life helping others in need by promoting their fundraisers on the radio speaking on the radio about lost pets and benefits  and actively participating in events around the county and town.  Everyone in the community looks forward to hearing “KC” on the radio and his guests are always very happy to appear on the show with him.  Kim dedicates her life to teaching Kindergarten children at South Hill Elementary School and she always greets you with a welcoming smile and friendly hello when you walk into the school.  She has taught and encouraged numerous children throughout her years as a teacher. Their son Caleb is a rising junior at Hampden Sydney College in Farmville VA.  Keith was diagnosed with a serious medical illness on May 21. He was hospitalized on June 23 at St. Marys Hospital and is now battling multiple health issues and has had a couple of setbacks due to complications. He had to have emergency surgery on June 28. More procedures will be needed once his labs become more stable. Doctors are not sure when he will be able to be discharged or transferred to an inpatient physical therapy program. They have mentioned the need of an inpatient physical therapy facility for 2-4 weeks to regain his strength after leaving St. Marys.   Due to his illness Keith is currently not working and the only income that is supporting the family at this time is Kim’s teacher’s salary.  Please help this family ease their financial burdens so that they can spend all of their energy helping Keith to recover; without the worry of funds.  Any help that can he offered to assist with food gas family lodging and medical expenses would greatly be appreciated.    Keith and Kim have spent their lives helping everyone in the community so please look into your heart and show them that they are loved and that you want to help them during this difficult time.",
