      "days_active": "1185",
      "days_created": "1703",
      "title": "Support Elias Coronas Family",
      "description": "A few years ago Elias Corona received the type of news that nobody would like to hear.  He was told that his body was under the attack of Cancer cells.  This news were horrifying to him however Elias never stopped fighting against it.  He made a promise to himself to fight with all his might against his illness and he did!  One year after his diagnosis he came back stronger than ever! Elias continued working as a Physical Education Teacher at Creighton School District in Phoenix Arizona and he had completed  15 years of service.     Cancer returned fearcer than ever a few weeks ago.  Today December 18 2016 in the early hours of the morning Elias lost his final battle to Cancer.  Elias’ wife Maria Alina  Corona and his daughter Diana stood by his side from the beginning of his struggle until his final moments. He often said that they were the strength and the engine that kept him going through it all.     Elias was and will continue to be a very loved and respected member of our community.  Elias earned the respect and admiration of all his students parents and colleagues from the moment that he started working in our district.  Elias absolutely loved his students and the families that he got to know through his work.  Whenever he saw a need he took proactive measures to help.  It was not rare to see him come to work sometimes carrying boxes and bags with shoes and clothes for students who desperately needed them.  For many years he also provided school supplies for many of his students.  Some of his colleagues even witnessed hip reaching into his pocket and put all the cash he could spare into the hand of someone in need.     As a colleague he was always willing to help. He often went the extra mile to help those who could use a hand.  His trademark was excellence respect and hard work. Elias was without a doubt a teacher who loved his profession and more importantly he loved his students and their families deeply.   Elias dedicated much of his life and talents to become one of the best educators in the field.     To many of us he quickly became a dear friend. Elias was definitely the kind of friend that everyone would love to have.  Elias was also an amazing husband and father.  Elias never missed an opportunity to share the great love and support he received from his wife and his daughter.  He was a man who honored his family and was not shy showing his love and appreciation for them.     Due to his intense fight against the devastating illness that finally took him Elias’ savings and earnings took a devastating hit as well. However giving up was never an option to them.  The whole family has worked together tirelessly not only to fight Cancer but to overcome their recent financial limitations.  Unfortunately even after all their hard work and conscientious efforts their resources are insufficient to face their current situation.  Medical bills and funerary costs have and will continue to add to their challenging circumstances for a while.     At this time we are asking those who have been inspired in any way by Elias’ life and example to join our efforts to help his wife  Maria Alina Corona with the many expenses that she now has to face alone on top of the unimaginable grief and sorrow of losing her best friend and companion.  Whatever contribution you are able to make will be personally received and managed by Maria Alina Corona.  We pray that the life of such a dedicated teacher and friend can touch your heart and at least on the economic area you will be willing to help to this cause and make their sorrows a little lighter for this family.     Thank you for your help love and support to this beautiful family you will be blessed for your efforts.     Thank you!!",
