      "title": "Curing corona",
      "description": "Sep.12 2015 our world changed. My 5 year old son was diagnosed with t-cell acute lymphoblastic leukemia. He went from a happy healthy energetic average 5 year old to a bed ridden cancer patient over night. When the doctor sat us down and told us my heart shattered and tears began to stream down my face and I instantly knew our lives would never be the same. Doctor visits hospital trips and chemotherapy will be our new normal. The money donated will help with hospital bills and anything that is required for treatment. From beginning til end treatment will take about 3 years. We dont have much and work very hard for what we do but this has made us realize that we are going to need more help than we can give on our own.  So please if you just give whatever you can it would be much appreciated and we will be forever greatful. Thank- you so much for your love support and prayers  -love the corona family",
