      "title": "Folkorico Dancer Cristina Scoliosis",
      "description": "Thank you for visiting Cristina’s gofundme page! Cristina was diagnosed with scoliosis in the fall of 2014.   This page was set up to help support Cristina’s scoliosis treatment. Cristina will begin her treatment journey by having Vertebroplasty (back surgery) during the winter break of 2015.  At which point 10 of Cristina’s vertebrae will be fused by doctors from Shriners Hospital of Los Angeles CA.    Cristina’s family was recently told that she has a very aggressive form of scoliosis. The conventional scoliosis treatment of bracing and physical therapy are not a treatment option for her because of the aggressive nature of her condition.  In the past 6 months her curvature has gone from less than 43 degrees to 58 degrees and is expected to progress at a rate of 2 degrees per month. With the onset of adolescent growth the curve and rotation of her vertebrae will continue to worsen. Our only treatment option is spinal fusion surgery. However because she is only 11 traditional fusion with titanium rods and bone grafting are not the treatment option that will have the most beneficial outcome for Cristina. Traditional fusion will limit her range of motion and not allow her to grow normally.   Cristina is full of life an amazing daughter an excellent student energetic artistic vibrant and a lover of Arts. Cristina has been a dedicated Folkorico dancer since the age of 7.  The emotional strain that she will feel of not being part of her dance troop will most definitely be more than the physical pain for her.    The road to recovery will not be an easy one physically emotionally and financially. Her parents have taken time off of their jobs to be close to their daughter but as we all know the bills of everyday living do not stop.There will also be upcoming medical expenses that will need to be covered and a long list of pre-surgery purchases that are needed  such as an orthopedic bed art supplies dog emotional support training and license (her dog is her best friend) alternative rehabilitation classes (Meditational training) financial burden of missing work  and the list goes on and on.        Please I ask all of my family friends friends of family etc. to please help us help Cristina and her family in this hour of need. Cristina is a fighter and she will overcome this!  We are kindly asking you to help support us in this endeavor. Any contribution you wish to make towards Cristinas surgery and recovery would be greatly appreciated. If you are not able to make any contribution by all means  Please share share share this page . If we get 1000 shares and 1000 people donate $1 then Cristina will have $1000. I am setting the goal high because just the expense of the bed is scary.    We thank you in advance for your generosity compassion and prayers for this little Warrior.    Blessing to you today tomorrow and always.    ",
