      "days_created": "1621",
      "title": "Woodroe Woods New School Fund",
      "description": "                                                Woodroe Woods is a private non-profit school that has been providing quality childhood education in the San Francisco Bay Area since 1974. Unfortunately the school directors are retiring and our beloved Woodroe Woods School will be closing in June of 2016.   There has been an overwhelmingly positive response from the teachers and parents of Woodroe Woods who are collaborating to open a new school. There is a profound need for quality early childhood education and childcare – for the children for the parents for the community. This is one profession where dedication and experience are crucial.   We need your help!  While we are working to find a new location there is an immediate need to raise funds to help with covering the significant expenses of starting up a new school. The funds we raise will go towards the purchase of the following items:  - Portable Classrooms - Architectural Lot Improvements - Moving Expenses - Permits - Licensing - Legal Fees - Playground Equipment (Climbing Structures Swings Slides Sandbox etc.) - Indoor Furniture (Tables Chairs Cubbies Bookcases Toy Storage Bins etc.) - Art Supplies - Appliances (Washer Dryer Vacuum Cleaner Telephone System Fans etc.) - Cleaning Supplies (Brooms Dustpans Mops Detergent etc.) - Office Supplies (Paper Ink Staplers etc.) - Office Equipment (Computer Copier Printer/Fax Filing Cabinets Desks etc.)  With roughly 160 Preschool through 2nd grade students attending Woodroe Woods each academic calendar year (and many families on the waiting list) the impact of this school closure on this community is great. Additionally our fun and educational summer camp enrolls an additional 50 students through 4th grade each year. Without this program many families will be scrambling to find quality childcare for the summer.  The children parents and teachers of Woodroe Woods will be so grateful for your support. With your help we are confident we can make this dream a reality. We look forward to serving this community for decades to come.  We will be posting progress updates on our Facebook page periodically. Please visit the   Woodroes New Beginning Facebook page    for updates .  Dont forget to like and share our updates to help us spread the word! Together we can make it happen.                     ",
