      "days_created": "1606",
      "title": "Coronas surgery",
      "description": "My dog is my world. My world has revolved around her since I brought her home. She is the reason I went to school and became a certified veterinary technician. She changed my life saved my life and made me a better person. Before my maternity leave i spent my days helping to take care of everyone elses  pets and now i really could use the help caring for mine.  About a month ago Corona started limping. As of last week she stopped putting weight on her back right leg. We went to the clinic and had x-rays done and had a specialist look at them. She tore her ACL and if not repaired she can over compensate on her other leg and tear her other ACL. The surgery is going to cost around 3000 dollars with a 6 week recovery. This couldnt have came at a worse time. We recently had our son at the end of September. With myself on maternity leave and only being able to only work weekends when I go back to the clinic this bill has really put us in a corner. I am not one to ask anyone for help like this and Im not sure how I even feel about it now but I love my dog. I would do anything to make her better. It just breaks my heart to see her in this much pain. We got a potty patch so she can go to the bathroom on the deck and avoid the stairs. Josh is going to build a ramp for her as well but these are only temporary relief. Anything would help at this point to relieve some of this stress. I just want her to feel better and be able to live her entire life span without pain.",
