      "description": "My name is rigo maldonado Alejandros brother. We are trying to raise money for his funeral  arrangements. Alex has been fighting diabetes since he was 6 years old. His condition worsen in the last 2 years. His kidneys where not functioning right he was going blind and a lot more problem s. On dec 27 he was found not responsive and due to the lack of oxygen to his brain he got a massive brain injury. He has been in a coma in the ICU for a week and doctors told us today (Jan 4) that they couldnt do anything else for him. Unfortunately this has hit us hard and at a time where financially struggling to so we are asking for your help. We thank you from the bottom of our hearts. Anything helps thank you and may god bless you all.",
