      "title": "Alejandras Memorial Fund",
      "description": "Hello   My sister-in-law Alejandra passed away suddently when she succumed to her injuries from a tragic accident. She is survived by her husband and her three beautiful young children ages 5 years 2 years and 7 months old.   Her passing was very sudden giving our familiy little time to raise money for her medical bills and funeral services. In addition to the money needed to raise her three young children. She was on life support for a week raising her medical bill to over $7000.00.  I would like to ask you for some financial support to give her proper barrial services and lift some economic burden off her husband and our familiy. Any little bit helps.  Thank you in advance for your prayers and support. God bless you!  Familia Valencia",
