      "days_created": "1490",
      "title": "Help Corona Surgery Fund",
      "description": "Many of you are animal lovers and dog owners and know what it is like to love your pet.  If you have seen the movie Marley and Me than you know how heart breaking it is when you lose your animal.  They become part of your family and it is devastating.   Well my friend Mike is facing a heart breaking situation  with his beloved pet Corona.  She has torn her Cranial Cruciate Ligament which is the same as a human tearing their  ACL.  Unfortunately surgery is the only way to take care of this problem.  Corona is in a lot of pain and can barely walk.  The surgery cost of $2500.  This is an expense that right now Mike is not able or prepared to pay right.  It breaks his heart that if Corona cant have this surgery he will be forced to have to put her down.    I would like to try and help Mike because anyone who knows him knows that he would do anything he could to help his friends and family if faced with this situation.  If there are any dog-loving friends out there who would love to contribute to Coronas surgery fund I know that Mike would be beyond grateful and so would I.  If you are at the stage of your life where you can give and want help your generosity would be much appreciated.  Sometimes in life we have to step up and help others and this is one of those time.    Any funds received that go beyond the cost of the surgery and physical therapy will be donated to a no-kill animal shelter or will be donated to vet to help someone else in need.",
