      "title": "Luis Corona",
      "description": "My name is Adrian Martinez.  I am a Union construction worker.  Recently  my friend and Union brother has been diagnosed with Stage 4 bladder cancer.  His name is Luis Corona. Luis has worked in the construction industry most of his life.  For the past 10 years he has exclusively worked for Firstline Systems a company specializing in framing and drywall installation.  Throughout the industry and throughout his own company Luis is well known as the guy that you want on your jobsite.  He gets the job done and always has a great attitude.   At 37 years old this is a huge shock to his family and friends.  Luis was in great physical shape and showed no signs of slowing down whatsoever.  His wife Yanet and their four children can only show Luis the love and support that he needs at this time. Firstline Systems and a few other companies have been passing the hat around at certain jobsites in the Seattle area.  All trades have contributed what they can.  With Luis beginning chemotherapy this week bills will start adding up quickly.  Our hope is to try to make this journey as comfortable as possible for the Corona family. It breaks my heart to see Luis this way.  He is so strong. He has an unmistakable presence when he enters a room.  To think of Yanet and the kids without that sense of secuity waiting and wondering what is going to happen next makes me feel ill.   I plan on doing everything I can to support this family through this journey.  Please say a prayer for Luis and his family.  Thankyou for listening.",
