      "title": "Natalia Casagrande",
      "description": "Natallia was a daughter a sister and a loving mother of two beautiful girls she was born in Montevideo Uruguay and raised in United  States she loved her family very much she was loved for so many people very happy and friendly she was very outgoing she will forever be missed. She lost her life tragically at the age of 24 on Tuesday Night May 31 her funeral cost and other expences will be covered with your help anything will be really appreciated our thoughts and prayers go out to her family. Thank You!!  Natalia era una hija una hermana y una madre amorosa de dos hermosas ninas nacio en Montevideo Uruguay y fue crecida en Estados Unidos ella amaba a su familia mas que a nada en este mundo fue amada por muchas personas era muy feliz y amigable era muy extrovertida por siempre sera recordada. Natalia perdio su vida tragicamente el martes por la noche Mayo 31 los costos de su funeral y otros gastos seran cubiertos con su ayuda cualquier donacion sera realmente apreciada nuestros pensamietos y oraciones son para toda su familia. Muchas Gracias!!",
