      "days_active": "1340",
      "days_created": "1340",
      "title": "Saul Coronas Funeral",
      "description": "         Due to the outpouring of love and support the Corona family has received we are offering a gofundme page to support them through these tough times.  After 2 months of fighting pulmonary fibrosis in the hospital Saul lost the fight and has passed away July 14th 2016. Leaving behind his loving wife children grandson as well as an enormous amount of family and friends.   Nobody deserves this type of fate especially not such a wonderful family. Saul will forever remain in our hearts thoughts and prayers.  We are humbly asking that you support the Corona family in this time of need. These funds will go directly to the Corona family to cover funeral expenses. Thank you for supporting in these times of difficulty.  May you rest in peace Saul Corona you will be missed loved and remembered by many.    Debido al sobre abundante amor y apoyo que la familia Corona a recibido hemos establecido una pagina de apoyo para estos tiempos de dificultad en cual estan atravesando.  Despues de dos meses de estar luchando en el hospital contra la enfermeded fibrosis pulmonar Saul perdio la lucha en Julio 14 2016. Dejando atras a su esposa hijos nieto y tambien una grande cantidad de familiares y amigos.  Nadie merece este tipo de destino especialmente una familia tan maravillosa. Saul siempre permanecera en nuestros corazones pensamientos y oraciones.  Humildemente estamos pidiendo apoyo economico para la familia Corona en estos tiempos de dificultad. Su apoyo economico sera dirijido para cubrir los gastos funerales. Gracias por su apoyo a la familia en estos tiempos de dificultad.   Que descanses en paz Saul Corona tu seras extranado amado y recordado por muchos.",
