      "days_active": "1336",
      "days_created": "1336",
      "title": "Joining Madonna House Apostolate!",
      "description": " Dearly beloved!  The Lord has invited me to become an applicant member of the  Madonna House  Apostolate on September 8th of this Holy Year of Mercy!  I spent last year living there as a guest. I lived their life; serving working praying and taking classes in their Fall and Spring Programs. At the end of my time there Jesus invitation in my heart to come back and stay became clear: to find Him love Him and serve Him wholeheartedly in this community one day at a time through Mary by His Grace. Theres many amazing things about the community and so many difficulties and things I would never choose but this yes is to His voice and invitation alone. May His mercy continue to pour and lead!    Im entering as an applicant... Applicancy is two years of formation after which God willing promises are made: For the glory of God and because I desire with my whole heart to respond to the call of Jesus Christ... to live in poverty chastity and obedience according to the spirit and mandate of Madonna House.     Their spirit in a word is  Nazareth;  to live love work and serve as the Holy Family in Nazareth in all aspects of life. This is also to live with the Lords Grace His command to love; particularly serving and being one with the poor. The community is often called The People of the Towel and the Water.    Living the Gospel and restoring the world to Christ is expressed at Madonna House in a variety of ways as is Loves endless creativity: - serving one another in all ordinary matters of life doing little things exceedingly well for love of Christ - spiritual formation programs for young people families and men discerning the priesthood - service to the local community especially to the poor - evangelization through publications (newspaper books music etc) talks retreats listening spiritual direction art music etc. - providing a divine milieu to guests who come year round (like me!) -currently 18 mission houses through Canada the U.S. Belgium Cariacou Britain and Russia each with their particular mission expressed by the heart of the Madonna House spirit. - restoring the earth through Christ through what the foundress called apostolic farming - Love love love never counting the cost....   Convinced that this call is not only for me but for the entire Body of Christ I invite you to support me :).  I beg for your prayers for me and for my family during this time! Our joined prayers in His Name will continue to unite us as a Body and will bring forth abundant graces.  All Glory be to God !   If you are also able to support in some financial way itd be an enormous blessing; whatever is in your heart and are able. I know theres many needs at this time certainly for you personally and for our Christian and non-Christian family so I do not ask unaware of that. I ask being unable to get there save by Gods Providence in whom my trust rests and in whom I place you as well. To get there I need to take care of some medical immigration and travel expenses (Im a Mexican citizen and currently living in Mexico) and some cover for the winter (Combermere is quite cold :O). If some of it winds up not being used by me I will make sure it goes to the poor.    Thank you for reading me and your support! I place all in Christs and Our Lady of Combermeres hands!   If you have any questions please contact me at sofia289@gmail.com! And if youd like to contact me a month from now this will be my address: 2888 Dafoe Road RR2 Combermere ON K0J 1L0 Canada   Count on my prayers for you!  ----------------------------------------------------------- MORE PHOTOS BELOW :D     Madonna House was founded in 1947 by Catherine (in picture) and Eddie Doherty.    The local view from an Easter morning hike to pray and worship God together Alleluia!   Sheep from the farm :)   The guests in the Fall Program... making Christmas wreathes from our Liturgy Class to celebrate the Nativity of Christ.   From a skit we did to celebrate Our Lady of Guadalupe. She isnt there because she switched costumes mid-skit.   ALL FOR HIM. This is from the road view at the main house of the Apostolate.   Our beautiful mother Our Lady of Combermere. :)              ",
