      "title": "Corona and her mom need help",
      "description": "we all know jean and corona and how much good she does for the shelter dogs making 1000s of ropes each year and distributing them to shelters either by car or mailing all at her own expense.   i am just another of jeans friends we meet thru the tragedy of justise for kodah and mush and have kept in touch since. i have had the privilege of meeting jean and corona and very proud to call them friends. Jean has been working in rescue for years for any type of animal but the one closest to her heart are the pitties. She needs our help now (she isnt asking way to proud for that) I am asking as her friend for all that know her and her work if we all donate even a small amount it will add up. also can you share this with anyone tha you think might hel and maybe the shelters she has helped in the past i will make the first donation myself. thank you so much for reading sharing and caring. sue banks walton ny 13856",
