      "title": "Rest in Peace Alec McReynolds",
      "description": "Friday morning July 22nd we lost one of the most beautiful souls we were lucky enough to have our lives touched by. Alecs mission in life was to put a smile on everyones face and help whoever he could no matter the cost. His huge smile and loving personality is something none of us will ever forget. As we attempt to get everyone whose heart hes touched together for a celebration of life many friends and family members of Alecs will be flying out to California to say their goodbyes. With flights being at their highest price during summer we are asking for the help of anyone willing to donate to make this possible for everyone who came in contact with his uplifting smile and ginormous heart. ONE HUNDRED PERCENT of the proceeds will go directly toward flights as he would not have wanted the money to go to him but to the people he loves.  His parents have also expressed to me that they are not  in any need of financial help for services etc.  Thank you all for your love and support for Alecs family as they mourn the loss of their wonderful son brother nephew and best friend.",
