      "title": "In Memory of Pasquale Pat  Corona",
      "description": "It is with deepest sadness to inform you of the untimely passing of Pat Corona Periop Support Assistant at St. Michaels Hospital after a brief illness. He was 46 years old.  Pat was an amazing man. He was nice and kind and always helpful and eager to lend a hand. He was soft spoken and always smiling. Those are rare qualities these days. He was a true gentelman and his passing is a tragety.   Pat will be greatly missed and remembered by all.  Our thoughts and prayers are with his family and loved ones.    Please make a generous donation all donations will go to Pats wife and 10-year-old daughter during this difficult time.    Information about visitation and funeral is in this link:   http://www.catholic-cemeteries.com/book-of-memories/2664863/Corona-Pasquale-Pat/service-details.php   ",
