      "title": "APU-2U",
      "description": "We are forming a student-run health clinic in Pasadena that will offer medical services to the homeless population who would not otherwise receive medical care. We will use this money to buy supplies such as clean socks trash bags scrub brushes antifungal cream dish sponges pumice stones feminine care products and other supplies we may need along the way. “The mission of the APU-2U Mobile Clinic is to provide holistic health services to meet the needs of the homeless population with passion commitment and skill.” Blessings!",
