      "days_created": "1314",
      "title": "Free daytime (all vinyl) open air",
      "description": "DJs have been saving peoples lives for days and nights on end. Help us help them help all of us.  Al Fresco is an open air party that supports vinyl DJs and the electronic music scene in San Francisco. This is a free event thanks to friends willing to support by providing a venue sound talent time and passion. This party started in December 2015 and has become somewhat of a monthly.  This time were trying something new. It goes a little somethin like this:  We want you to have some fun on us. Well on all of us. And we want to support our local talent while were at it.   So this is a crowdfunding campaign to build a better and better party as we reach specific funding goals. So keep reading and party on Wayne.   $0-100 - We have a party  $100-400 - DJs get paid $400-800 - DJs get paid 2x (so they can buy more wax!) $800-1200 - Sweeter soundsystem  $1200-1400 - Extended hours $1400-2000 - Special Guest (TBA) $2000-3000 - Limited time open bar  $3000-4000 - Extra cheap drinks all day $hit if we keep going well slap some realness on all yall   The event is scheduled for Sunday 28 August 2016 and will be recorded and streamed live for all you beautifuls that cant be here.   For more details visit  www.alfresco.haus.   Facebook event:  https://www.facebook.com/events/261892027530156/    Note: First and foremost vibe. is. key. Your support is what will make this happen and we appreciate both your monetary contributions and sharing this campaign with your legit network of friends whether they/you live in SF or elsewhere. Promotional channels are being discussed in order to maintain intimacy amongst a solid crowd that appreciates quality in every aspect.   A buck $2 5 10 15 20 25+ if you love the idea and to show your love and support to the DJs everyone involved and most importantly so that others can enjoy some nice tunes and vibe from the kindness of the community.   More details coming soon.   So Wayne... Shall we party?   Location: 1605 Jerrold Ave. In beautiful Bayview SF.        ",
