      "days_active": "1279",
      "days_created": "1313",
      "title": "Ians Fight Against Cancer",
      "description": "Hi! My name is Erin and I am raising money for my best friend Ian.  We have been friends since 4th grade and overcame many obstacles together.  Now at 23 years old we face another.  Ian an avid surfer and skateboarder is taking a little break from his favorite activities to fight a battle:  rhabdomyosarcoma . Its a tough cancer to beat but we are all praying for a miracle for this amazing man!  ?Since most medical treatments have been exhausted he has started a new and natural journey towards health.  I am a registered nurse and I know just how beneficial a healthy lifestyle is for anyone especially Ian.  The current plan is to get him to a  naturopathic treatment center  on the Big Island of Hawaii. Here Ian will have full access to naturopath doctors fresh and nutritious foods and vital treatments. Since expenses are the last thing on his familys mind I would like to personally ask anyone to join in his fight by donating any amount.  On behalf of Ian and his family I thank you for your generosity in hearing his story and contributing. Any amount is truly appreciated. God bless!  Wanna learn more about this cool guy? Check this out:  Meet Ian - Surf News Network    ?Ian and I as freshman in high school at winterball 2008.",
