      "title": "Help For 84yo Icecream Man MR Corona",
      "description": " Eleuterio is a 84 year old man that sells icecream and churros on the streets of South Chicago his knees are giving up on him due to his age and his wife is battling beast cancer at the hospital he has to take care of here and buy her medicine because they have no government aid due to their legal status in the USA. He has been pushing his little cart around for almost 20 Yeats sellING ice cream on the summer and churros/corn in the super cold Chicago winter under the snow. This is a very hardworking who has never asked for anything on his life. I believe it is time now to help him thinking of him as if he was our grandpa or father he really is in need of this help please touch your heart and donate one dollar makes a big difference! Lets help him reach his goal so he can rest at least this winter and take care of his ill wife!!! PLEASE SHARE SHARE SHARE. DIOS los bendiga!    ",
