      "title": "Rebuilding Imagina Menlo!",
      "description": "We are writing to let you know about the sad events at Oscar and Marias daycare; there was a devastating fire at their home awhile they were out on Sunday. Thankfully nobody was injured - but the entire back of the home burnt down including the backyard and daycare materials.   Currently Oscar and Maria are looking to restart their lives and business - this was not only their home but their sole source of income.  For those of us lucky to be part of the extended Imagina family; we know what a labor of love it has been to create an amazing environment for our children.    The money raised will be used to rebuild Imagina Menlo at a new location.  The list of expenses includes - moving expenses security deposit   purchases of things needed to create a new thriving daycare (outdoor play shelves blocks)!  Thank you for your support - the love   encouragement from this community has been overwhelming.",
