      "title": "Save Ananda Scotts Valley",
      "description": "   Dear Friends  Ananda Scotts Valley is in need of our support. Doug and Kristy Andrews have been running ASV for some time now and are finding it difficult to keep up with the centers financial obligations. For the past two years Kristy has been in remission from breast cancer but recently discovered the cancer has returned is now stage four and has metastasized.  Currently Raghu Clark and the rest of the ASV team are continuing to magnetize the center and they may need a couple more years to hit a break-even point in their finances.  ASV needs to supplement the monthly rent of $5000 for the temple and boutique. The money raised will be used to help maintain ASV. It will allow Doug to focus on and be with Kristy in this trying time of her life.  Lets all unite in Divine Mothers love to help our dear friends in their time of need. If you are able please donate towards our goal of $30000. The money will go to making ASV stable so it may continue to be a beacon of Masters teaching in Santa Cruz County.  With Gods love and strength we can achieve this!  In Masters love  A master devotee",
