      "days_created": "1242",
      "title": "Lorenas Treatment Fund",
      "description": "Three weeks ago Lore was diagnosed with stage 4 Tongue Cancer (squamous cell carcinoma). Within these few weeks she has gone from feeling discomfort to not being able to talk or swallow. On 10/20/16 her physician let her know that he will need to remove the majority of her tongue lymph-nodes and possibly her voice box. I refused to accept this debilitating surgery and contacted the University of Chicago who specialize in treatment without surgery. Lorena and Pablo are going to travel next week to meet with the doctors in Chicago to seek treatment there. I have opened this GoFundMe page to help support Lore during this crucial time. We  are hoping for the best with the University as they have been successful in cases similar to Lore’s. No donation is too small and please share this link as that also helps. This has been tough on the family and we are doing everything possible to get Lore help.  Thank you so much for all your support Lore and Pablo really appreciate. I appreciate it. We love you Lore and want the best possible treatment for you.",
