      "description": " Peter my loving son with a beautiful smile that warmed everyones heart that he touched. He was a 2015 Fletcher graduate drummer in the band swim team athlete currently a FSCJ student and a volunteer lifeguard in Jax. Beach. Very dedicated to all that he was involved in . My sons life was tragically taken from him by a coward who shot him in the face. This week I have to bury my only child Peter Anthony Perez-Corona. My son was a beautiful loving soul to me and to all that knew him. His warm tight hugs helped me through every situation in my life. I am facing a $25000 burial fee at this time. Please help me raise the money for this. Thank you all for your prayers loving thoughts and shared memories about my sweet boy Peter. God Bless from the bottom of my heart.  ",
