      "description": "I am Maritza Corona and it is with deep sadness that I announce the passing of Brenda and Jeremys only son Jeremy Jr. who was born April 9th 2016 and passed away October 29th 2016. I am asking for your help in paying for the funeral expenses for this beautiful family. They have two other young children Natali (4 years) and Andrea (3 years) and need time away from work to grieve as a family. As their aunt this hits so close to home and I would appreciate your prayers and donations to help my niece during this tragedy. If 100 people donate $10 thats $1000...From the bottom of our hearts we ask you to help us give this little angel a beautiful last goodbye. The funeral will be this Friday November 4th 2016. God Bless You!",
