      "title": "Luis Coronas Funeral Costs",
      "description": "Alfredo Corona has been a part of the BP team for 10 years. His oldest son Luis Corona also worked at BP as a temp for a couple of months earlier this year. Unfortuntely on October 29th Luis Corona became a victim of Chicagos street gun violence while making a delivery for his work as he was shot to death on the spot. The poor kid was in the wrong place at a wrong time. We came to know Luis while he worked at BP and found him a very loving respectful energetic and hardworking individual. He carried the Coronas family moto of hardwork and dedication quite well. Just like the Corona family we are all shocked with the unexpected demise of their son and as members of his family at work we would like to raise some $$ for the Corona family to assist with the funeral-related costs. Its just to show Alfredo and his family that we are all thinking of them and are keeping them in our prayers.",
