      "goal": "50000",
      "donators": "14",
      "days_active": "1229",
      "days_created": "1229",
      "title": "Help Debbie Fight Pancreatic Cancer!",
      "description": "Debbie was diagnosed with stage 4 pancreatic cancer in September 2016. This unexpected diagnosis hit hard on her and her family. She is a daughter mother wife grandma and will fight this battle with God on her side!   Because pancreatic cancer is the deadliest of cancers there was an urgency for treatment. The doctors werent concerned about it too much and have not tried their best to help her. There was no other choice but to look for better options. Her kids searched for hours and hours at a time and came across a better solution. The clinic that we have been led to is American Holistic in Mexico. The clinic features more highly effective treatment options under one roof than any other cancer clinic and it has done well in treating pancreatic cancer patients. This is an expensive program though and is not covered by insurance. The treatment costs range from $45000 to $50000.   Debbie has finished treatment but in the meantime the medical bills(including treatment) are piling high. There has been so much support from family and friends to make the trips to and from Mexico the accommodations and meals. All of this support is so much appreciated and we love you all for reaching out and helping Debbie and the family in this time of need. What we are asking is that you donate any amount that you feel in your heart(it doesnt matter how little) and please spread the word and share this page.   We refuse to let this disease tear us down. We are in good spirits and we continue to rely on God as the joy of the Lord is our strength and the strength of the Lord is our joy.   Our family has set a goal to raise $50000 and all funds donated would go specifically to the costs of her medical expenses (plus the fees charged by GoFundMe).   Throughout this journey we have been blessed with so many people giving us prayer and love. Several have asked what they can do to help. Contributing to the cost of Debbies treatments would be a blessing. We would be so grateful for your donations.   We have strong faith to believe that God will move people to help us and we do believe He will completely heal Debbie in Jesus Name!   As a mother wife grandmother Debbie still has so much more to experience with her family. Thank you so much to all of those who would like to help. God bless you abundantly for your generosity. Your loving gift will be received with great appreciation!   Please continue to keep Debbie in your prayers as she fights this disease for her family friends and other survivors of pancreatic cancer.   Our family thanks you for your support. Please share our story to help us reach our goal.",
