      "title": "Emilys Journey",
      "description": "In June 2013 my sweet daughter Emily lost her left eye to cancer. Her scans and reports were clear until two weeks ago. She went to the emergency room for what she thought was a gallbladder attack. The doctors informed her that her melanoma had spread to a few places in her abdomen including her liver. Monday November 14 she meets with her team of oncologists to discuss what to do next. I would like to ask all of my friends and family to help out in any way that they can. Emily is facing many co-pays transportaion costs and medical expenses. Her sweet husband is doing all he can but is having to miss work to assist her during this very difficult time. With two small children their hands are very full. Thank you in advance and please share her journey. God bless you all and please keep our family in your prayers.",
