      "title": "Rocios Fund",
      "description": "On November 11th following her brave battle with a rare liver disease we lost our beautiful sister Rocio.  This is a terrible blow to all of us; her friends and family.  We want to offer our love and heartfelt condolences to all of her loved ones and to her husband Jesse Corona who stood by her side through it all.  Jesse and Rocio are beloved members of our Los Angeles bartending community.  Rocio and Jesse met each other at the Golden Gopher; the downtown Los Angeles bar which has served as home and headquarters to so many of us in the 213 Hospitality family and the DTLA bar community.  Rocio was a kind funny smart and loving woman whose warmth will be dearly missed by the many people whose lives she touched.  Rocio’s family is grieving during this difficult time.  As her friends and colleagues we would like to raise money to help relieve Jesse and Rocio’s family of her funeral and final expenses.  If you could please help by donating even a few dollars it would be greatly appreciated.  If you cannot donate please keep Jesse and Rocio’s family in your thoughts and prayers.",
