      "title": "En memoria de María &Octavio Corona",
      "description": "Es con mucha tristeza que les dejamos saber que nuestros padres Octavio Corona Pena y M. Transita Monroy García fallecieron inesperadamente el 2 de enero 2017 en México.  Nuestros padres fueron muy bien queridos por sus hijos y sus amistades. Deseamos cumplir los últimos deseos de nuestros padres y celebrar sus vidas todos juntos. Mis padres seran transferidos a Chicago desde Mexico y enterrados cerca de nuestra casa procesos que nos cuesta no solo gran difficultad emocionalmente pero también bien económicamente. Lamentablemente estamos enfrentando un tiempo muy dificil y cualquier ayuda es bienvenida.   Si desean ayudar hemos creado este recaudación de fondos para pagar por los costos de los procesos.  Gracias de parte de sus hijos Nelly Octavio Jr Roberto Patricia y Jennifer  --  It is with great sadness that my siblings and I announce the unexpected passing of our parents Octavio Corona Pena y M. Transita Monroy García on January 2 2017 in Mexico.   Our parents were greatly loved by their children family and friends. We wish to grant the final wishes of my parents to commemorate their lives amongst their loved ones. My parents will be transferred from Mexico to Chicago and buried close to our home. These are processes that are not only emotionally distressing but also a great financial burden. In these difficult times any type of help would be profoundly appreciated.  If you wish to kindly help us we have created this fund which will help pay for the costs we will encounter.  With Gratitude  Nelly Octavio Jr Roberto Patricia and Jennifer Corona",
