      "description": "Urgent Call To Action:Can you please donate money for free meals? The Community Cafe has 3 free cafes and a Food Truck in the Charlotte area.  We are an all volunteer 501C3 charity. Due to the Coronavirus Crisis we are serving meals drive up to-go style only. No longer can we invite guests inside for a meal and conversation.Our weekly meal count has doubled and our donations are down. People are being layed off in historic numbers. People call us to add Food Truck stops desperate to feed their children. We never ask for private information.  All are welcome here. All can take whatever they need.Even $1 will make a difference!  and all our thanks!The Community Cafe www.communitycafecares.com ",
