      "title": "In Rememberance of Fabi ",
      "description": "On January 3 2017 my sister Fabiola Rodriguez never made it back home. She was a woman of grace and vivacity always cheerful to be around. Despite this her life was unexpectedly taken away while she vacationed in Mexico. She never got to wake up to kiss her daughter Viviana in the morning or to comfort her oldest son Adrian who was waiting for her return.  Words just simply cannot express how much pain this immense loss has inflicted on our entire family especially for her children. We are asking for your prayers during this difficult time including any support to cover her funeral costs and expenses. Any amount will be genuinely appreciated as it would greatly help to lessen the financial burden and give her the proper honor and memorial that my sister deserves.  Please feel free to share this to let others know. God bless. Isaiah 33:2 Lord be gracious to us we long for you. Be our strength every morning our salvation in time of distress. //  El 3 de enero del 2017 mi hermana Fabiola Rodríguez nunca regresó a casa. Era una mujer de gracia y vivacidad siempre alegre cuando estaba cerca. A pesar de esto su vida fue inesperadamente robada mientras estaba de vacaciones en México. Ella nunca llegó a despertar en la mañana para besar a su hija Viviana  o para consolar a su hijo mayor Adrian que estaba esperando su regreso.  Las palabras simplemente no pueden expresar cuánto dolor ha causado la inmensa pérdida a toda nuestra familia especialmente a sus hijos. Estamos pidiendo sus oraciones durante este tiempo difícil incluyendo cualquier apoyo para cubrir sus gastos y gastos de funeral. Cualquier cantidad será genuinamente apreciada ya que sería de gran ayuda para disminuir los gastos financieros y darle el honor y el recuerdo que mi hermana merece.  Por favor siéntase libre de compartir esto para que otros sepan. Dios los bendiga siempre. Isaías 33: 2- Señor ten piedad en nosotros te anhelamos. Séa nuestra fortaleza cada mañana nuestra salvación en tiempo de angustia.",
