      "description": "Richie Corona a dedicated single father lost the battle against cancer on Saturday Jan 28th 2017. He is survived by his daughters Nicole age 22  and Kristen a high school senior age 17!  Their lives has been disrupted by his untimely death and need your help for their future.. Please consider donating to this cause helping Richie achieve his dream and desire for his daughters to be happy and successful in their future lives. Kristen aspires to attend college and pursue a Bachelors of Science Degree in Nursing  and Nicole is committed to furthering her pastry/management passion in the culinary field. Thank you for your support and appreciate your kindness!",
