      "goal": "50000",
      "donators": "16",
      "days_active": "1136",
      "days_created": "1136",
      "title": "Kathryns Chance @ a Lifetime Dream",
      "description": "Everyone has a dream THAT THING theyve always wanted to do if all the right doors would open. Twenty years ago I sat in a British Literature class at CWU and found my dream. I wanted to study in London. It was always a pie in the sky/wouldnt it be cool if sort of dream. My practical side kept going to school built a full-time teaching career and established a pretty rewarding life. I am a professional student. Which probably explains why I am also a teacher. Meanwhile that dream always floated out there--waiting.        Until last spring. The dream sat down in my lap and took up residence when I was least expecting it. Through a series of open doors and gentle shoves from God I applied for a Fulbright Scholarship to attend an innovative interdisciplinary program at  Royal Holloway  University of London. To complete my scholarship application I had to also apply to the school itself for admittance. Deep in my heart I knew that I knew that I knew God had led me to this opportunity and  I could not ignore it.      Now I face a conundrum. I have been accepted to Royal Holloway to begin my  program  in September 2017. However I learned at the end of January that I did not receive the Fulbright Scholarship which would have covered a large bulk of the cost for me move to attend school live in and then return from the UK.  My conservative estimate is that I need at least $50000 USD to fund everything for a year.        Asking for money in this fashion is WAY outside my comfort zone. In the past I worked my way through school. Slowy but surely everything has always been paid off.  However that isnt an option this time around. I need the bulk of the money up front and the rest to live on for the year since if I can work it would be only on a limited part time basis. Im applying for other smaller scholarships but even together they would only cover a small fraction of the cost -if I win them. A friend recently encouraged me that I only need 5000 people to give $10.  I dont know 5000 people. But the six degrees of separation adage is totally true. So even though I dont know enough people all my friends do! If God has truly called me to this opportunity then He will fund it - Im just trying to make all the options available to Him! Every prayer share and dollar given brings me one step closer to a dream fulfilled.",
      "default_url": "one-time-chance-lifetime-dream",
      "has_beneficiary": "FALSE",
      "media_type": "3",
      "project_type": "1",
      "turn_off_donations": "FALSE",
