      "title": "Wolfie Corona",
      "description": "My name is Sukey. Ive set up this page for my brother in law. On December 2016 Wolfie was diagnosed with stage 4 colon and liver cancer. Hes started chemotherapy and should be going through this treatment for the next 6 months. The life expectancy he received was 3 years. Today he is being sent home from the hospital under hospice care as his cancer has rapidly spread and affected other organs. We have not given up hope that a miracle can happen and he will get better.   The cancer center suggested a go-fund me page as a way to help with out of pocket expenses as Wolfie will be not be able to work for a while.   We have been fortunate and blessed with many friends and family keeping us in their prayers.   I want to thank you all from the bottom of my heart.",
