      "description": "In the beginning of February Brandon Corona began having severe headaches and vision issues. It progressed to the point he went to the emergency room for treatment. Going undiagnosed Brandon was sent home where his condition continued to worsen. After having a couple episodes of passing out his family brought him home to South Florida for additional testing.  Doctors were able to discover a serious brain condition that required immediate surgery. Following the surgery he was sent home to recover where his symptoms returned… this time worse. Rushing back to the doctor’s office it was discovered that the previous surgery was not successful and another procedure was needed. After the second emergent brain surgery doctors found the pressure on his brain needed to be better controlled. Brandon is scheduled today (March 2 2017) to undergo a third brain surgery to remove a portion of his skull to relieve the pressure pushing on his brain.  The Corona family has been a pillar in the community. Hector Corona Brandon’s father is a Battalion Chief with the Margate-Coconut Creek Fire Department. He has been a member of the fire department for 29 years and has dedicated his life to helping others in their time of need. Brandon’s mother Julie Corona has been a registered nurse for over 30 years. She has worked in emergency medicine home healthcare and hospice care. Both of his parents have given support to many families throughout the community by hosting fundraiser event and giving their time volunteering.  During this tough time for Brandon Julie has taken time off work to care for him and be by his side. On his time off Hector is an instructor at the fire academy and has stepped away to care for his son as well. Medical bills and loss of work has put a significant financial strain on this amazing family.",
