      "title": "Final Arrangements/Teresa Jackson",
      "description": "Our dear sister Teresa selfless and dedicated with a heart for others succumbed on 3/30/17 to a rare form of cancer which affects only 1 in 5 million and is extremely aggressive.  As with so many women who have suffered through the ravages of an abusive relationship she was robbed of many of life’s pleasures except for those which her 3 children have brought her.  Working through hardships unspeakable for most people including several occasions of homelessness and as the only source of income for the past 25 years her indomitable spirit to provide for her family drove her to continue go to work under grueling physical discomfort up to 5 days before her passing while in hospice care.  Typical of Teresa the day before her death continued to beg us to take her to work for she had to make sure that the company payroll was processed.  Not unexpectedly those who have had the privilege of knowing Teresa have wanted to visit with her and the stream of visitors was purely heartwarming and delighted Teresa with some coming from as far away as Hawaii.  Unfortunately the veracity of this illness left little time to prepare and plan for final arrangements.  The family has decided that her remains will be returned to her native country the Philippines where the family has arranged for her to at last rest beside her mother in Cebu.  Her two sons have stepped up to take charge of her affairs but are facing challenges as all the kids were living at home and as of this week will not have a home.  It is important to note that her sons have inherited her drive and both are working several jobs and going to school.  However the cost of final arrangements and expenses of the transition exceed their capacity.  It is the desire of the family to get the boys into a shared apt. and set up for a success; additionally for them to accompany Teresa back to the Philippines on her final journey.  Neither has been to their mothers’ native land.  Anyone who has spent time with Teresa always left feeling uplifted.  Now let’s get together and left up the rest of her family in her behalf!  Our goal is to raise enough money to help with the expenses of caring for her remains and prepare the kids for a life without their mother best friend and provider.  Additionally if funds are available we would like to have them personally accompany her to her final resting place in the Philippines where her loving family is ready to receive her.   Undray Jewel Jackson II (Konkie to some) Teresas oldest son is designated as the custodian of the funds raised in this campaign.  He has stepped up and is taking care of all the responsibilities.    Life celebration service will be on April 28 and we will update everyone with the details later.  Again thank  you all for your tremendous support!",
