      "title": "Michelle Toro Chardon Funeral",
      "description": "                        Michelle Toro Chardon passed away Saturday April 22nd in West Jordan Utah.  Her only sibling my dear friend Madeline Corona brought Michelle to Utah last year from Puerto Rico to receive more aggressive medical treatment for her battle against advanced stage breast cancer.  Michelle was diagnosed at age 34 and received basic treatment in Puerto Rico.  A more aggressive treatment was required and The Huntsman Cancer Center was a blessing to her but all their efforts could not save her life.  Her cancer spread to her bones and lungs causing her severe pain.  Although fragile she fought every day with determination to live for her 3 young children and taught everyone how to be couregous.  She was so loving happy and had a great heart and was an example of great faith.   She always gave others good advice and encouragement and taught everyone to accept Heavenly Fathers will.  Michelle was  37 years old when she left this earth life and leaves behind her husband and 3 children.  Her immediate family was recently able to join her in Utah along with her parents and be with her through the last stages of her valiant fight against cancer.  Michelles wish was to return to Puerto Rico for her interment.  Madeline and her family of 5 are doing all they can to assist Michelles young family with funeral and travel expenses and will need to travel to Puerto Rico themselves.   Funds raised here will be used for travel and funeral expenses.                        ",
