      "title": "Freddy Corona Battling Cancer",
      "description": "I Had the privledge of meeting Maria and her son Freddy at the Ronald MacDonald house in Seattle cancer. Maria shared her story with me as we went to the childrens hospital she said how much cancer her hit her not only emotionally but also finalcially. With her having to leave her job for 6 months and make all of the trips from Tri Cities to Seattle Childrens she did this for Freddys treatment. His treatment plan is 4 YEARS long For the first 6 months Freddy has to be in Seattle staying at the Roanld Macdonald house For the other 3 1/2 years he has multiple trips every month from Tri cities to Seattle Here is Maria her son and their families story.   Around November 2016 Freddy was having cold symptoms fevers sweating a lot loss of appetite and excessive tiredness. This continued for a few weeks and Maria took Freddy to the Doctors multiple times the doctors didnt give them much information they said that Freddy just had a Virus. This continued for a few more days until she took him into Urgent Care. they were then sent to the ER because they didnt have the resources that they needed. When they got to the Er the doctors finally started to run different tests when they got the results they were RUSHED to the Seattle Childrens Hospital! At that point Maria recieved the information that her son had cancer.",
