      "days_active": "1017",
      "days_created": "1017",
      "title": "a helping hand for Jessica",
      "description": "Ive never been in this boat before.   I dont mean Ive never asked for money... The boat I am talking about is the one where I actually am open to getting help for myself for my traumas.  So I did it; at the risk of drowning of the heaviness of real life responsibilities and financial stress.    So heres a brief story of what   why...   I have recently been discharged from Inpatient Hospital that treated me for Major Depressive Disorder (MDD) Post Traumatic Stress Disorder (PTSD) General Anxiety Disorder (GAD) but mostly Id have to say I was admitted due to the suicidal plans Id made for myself.  My friends and colleagues may have noticed Ive been absent for the last few days weeks months and years.  More of the latter that is.   To some it sounds heavy... well it was and it is... still is.     To others this may sound like an irresponsible reason to need money and they may think I should feel ashamed.  Well guess what I already do.  Hah!    But what I finally believe is a better truth is that I deserved to finally take care of myself properly which is something Ive needed to do all my life.   So here is the jist I am need of funds to help me stay in my apartment until my Short Term Disability extends my claim date and they send that info to payroll  so I can get paid 80% of my check. Yep 80%...   While I have tried my hardest to find a way to make some quick money -- i.e.: walking the streets of Preston (jk) Loans 401k withdrawal and even looking into non profits for help; My efforts have been unsuccessful.    Today I am facing eviction and I dont know what else to do.  If you can find it in your heart to help in any small way.  I will be truly greatful.  My goal is to run a report to see who contributed and I promise I will reimburse you.    Thanks for caring enough to read this.     Love Jess Jessi Jessi Yesi JC Corona and all the variation of nicknames i am known for.   P.S. I am in continued care in case you were worried.  I am doing my best to get the most of the help I am receiving medically.  #Nostigma #suicideprevention #gettingonmyfeet #helpmeimpoor #Istillhaveasenseofhumor #anythinghelps #iappreciateyou",
