      "title": "Open heart surgery for Pastor Vidal",
      "description": "Some of you may know him as a a man of God a teacher a friend a pastor a mentor or a coworker. I simply know him as Dad. My sweet selfless profoundly wise father is 52 years old and has been healthy his whole life. He recently completed his PHD in leadership and was scheduled for graduation June 16 2017. Unfortunately he will not walk during a much anticipated graduation and instead will be confined to a hospital bed for post surgery recovery. During a routine checkup he was told he had artetial plaque in 6 different locations and would immediately require an extensive quadruple bypass. As you can imagine this was heartbreaking news for someone who is active and has never had any major medical issues. After the surgery he will be out of work for several weeks which will cause a heavy financial burden on him and his family. After insurance pays their part he is still left with a bill of $75000. This is going to cost a lot of money time patience people and God’s hands with the surgeons. So all we are asking is for a little help to lift the burden off his shoulders so he can just focus on getting better. The funds would be used to offset the costs of bills. Every dollar counts and we just ask that you give as God has given us. In the event that you cannot donate prayers are free and are always needed. Thank you and God bless.  ? 2 Corinthians 9:7 ? Each of you should give what you have decided in your heart to give not reluctantly or under compulsion for God loves a cheerful giver.   ",
