      "title": "Julianna Corona Memorial Fund",
      "description": "                                                Julianna tragically lost her battle with brain cancer on Saturday June 17th.  A young life that was taken away from us way too soon.  Julianna was a kind caring mother to three young beautiful girls and a devoted wife to her husband Pedro.  She fought extremely hard for her family against this deadly disease from the inital diagnosis on 7/18/16 until this past Saturday.  The family wants her to be remembered as the amazing and strong person she is.  She no longer has the pain and suffering and is at peace but her husband and the girls need our support to give her the service she deserves.  The Corona family needs our help at this extremely painful and difficult time with final funeral services.  Please try to help with whatever you can no donation is too small and it will go directly to the family in their time of need and mourning.  Consider helping this amazing family with this heavy burden we appreciate everything.  Thank you from the family and friends.            ",
