      "days_active": "961",
      "days_created": "961",
      "title": "Maria Lety Paz Memorial",
      "description": "It is with great sorrow and a heavy heart that I reach out to inform you all of our mothers passing Maria Leticia Paz. She was a hard working devoted single mother of three and grandmother of one beautiful baby boy. The greatest mother and role model we could have ever asked for. She was the hardest working person we knew day in day out battling this cruel disease she mustered up the courage and very little energy she had to go to work always with a smile on her face all for her children. She fought cancer with all of her strength it took everything from her but she never stopped striving for a better future for us. She touched many lives in positive ways always lending out a helping hand and never expecting anything in return. She was loved by many and will forever be remembered as a generous beautiful woman that always put everyone before her. We know she is in a better place with no more agony and pain resting in peace and that eases our hearts. Mom we will miss your sense of humor and that never give up attitude that characterized you. You were our rock our everything but we will try our best to emulate your strength as you would want us to. Siempre estaras en nuestro corazon please visit us in our dreams Mami te queremos de aqui al cielo. - With love Candy Diana   Dorian.    This gofundme page has been created to honor her presence. The funds raised here will go to help support her family in their on going time of need.This page will also be used to keep those who love Lety updated on her final arrangements Letys family is grateful for the love and support.",
