      "title": "Help the Coronas life after Harvey",
      "description": "My name is Crystal and I am a family friend of Elizabeth. We are from Houston Texas and weve recently been hit with a catastrophic hurricane named Harvey. Elizabeth is the kindest soul Ive ever known. She would give her last dollar to help someone in need. This week hurricane Harvey stole everything from her. After being rescued and evacuated from their home theyve come back to find there is nothing left. They must find a new place to live and start all over. She has two sweet boys that went through the tragedy with them. They have already applied for FEMA assistance and are on wait list but need a hotel or somewhere to stay immediately. A single dollar can make a huge difference! I plan to have the funds sent directly to Elizabeth once this campaign is over. Please join me in raising a small fund to help the Coronas find a place to live and the ability to start over.     ",
