      "title": "Ramon Coronas Funeral Cost",
      "description": "Thank you everybody for reading. This is Nic Corona my father Ramon Corona passed away on September 12th 2017. It was very unexpected and has left my family shocked. We have not been able to locate any kind of life insurance and I would just like to bury my fathers remains as respectfully as possible. He was a veteran and will be buried at Miramar National Cemetary with full military honors which covers the burial but does not cover a memorial service urn or anything else that goes along with saying goodbye to a loved one. I have already had to make the difficult decision to have him cremated because my family could not afford to have his body transported to California. I appreciate all the help and support in this diffucult time for my family. Thanks to my family and friends who have already helped and supported with some costs. If anybody can donate anything at all it would be deeply appreciated.",
